.portfolio {
  padding: 0 24px 58px 24px;

  @media (min-width: @tablet-width) {
    padding: 0 48px 58px 48px;
  }
}

.portfolio__title {
  font-weight: 700;
  font-size: 28px;
  line-height: 32px;
  color: @highlight-color;
  padding: 0;
  margin: 0 0 35px 0;
}

.portfolio__subtitle {
  font-size: 24px;
  line-height: 28px;
  color: @text-color;
  padding: 0;
  margin: 0 0 18px 0;
}

.portfolio__list {
  .list-reset();
  width: 100%;
  display: grid;
  gap: 28px;
  position: relative;
  margin-bottom: 48px;

  &--three-columns {
    @media (min-width: @tablet-width) {
      grid-template-columns: repeat(2, 1fr);
    }

    @media (min-width: @desktop-width) {
      grid-template-columns: repeat(3, 1fr);
    }
  }

  &:last-child::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 5px;
    background: linear-gradient(to right, rgb(112, 43, 126), rgb(199, 141, 189));
    bottom: -48px;
  }
}

.portfolio__item {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 0;
  padding: 4px;
  outline: 1px solid @separator-color;

  @media (min-width: @tablet-width) {
    flex-direction: row;
  }
}

.portfolio__link {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  &:focus-visible {
    outline: 1px solid @highlight-color;
    outline-offset: 2px;
  }
}

.portfolio__image {
  width: 100%;
  object-fit: contain;
  display: block;
  background-color: rgb(246, 246, 246);

  &--only-one {
    @media (min-width: @tablet-width) {
      max-width: 349px;
    }
  }

  &--half {
    @media (min-width: @tablet-width) {
      min-width: 331px;
    }

    @media (min-width: @desktop-width) {
      max-width: 546px;
    }
  }

  &--half:first-child {
    margin: 0 0 4px 0;

    @media (min-width: @tablet-width) {
      margin: 0 4px 0 0;
    }
  }
}
