.header {
  width: 100%;
  min-height: 70px;
  background: linear-gradient(to right, rgb(213, 75, 36), rgb(254, 186, 17));
  padding: 10px 52px;
  display: flex;
  align-items: center;
  justify-content: center;

  @media (min-width: @tablet-width) {
    padding-top: 20px;
    padding-bottom: 20px;
  }
}

.header__slogan {
  font-style: italic;
  font-size: 20px;
  line-height: 24px;
  letter-spacing: 0.05em;
  color: rgba(255, 255, 255, 0.8);
  padding: 0;
  margin: 0;

  @media (min-width: @tablet-width) {
    font-size: 24px;
    line-height: 28px;
  }
}
