img {
  max-width: 100%;
  height: auto;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.page {
  // для sticky footer
  height: 100%;
}

.page__body {
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  color: @text-color;
  height: 100%;
  margin: 0;
}

.page__container {
  display: flex;
  flex-direction: column;
  width: 320px;
  margin: 0 auto;
  //для sticky footer
  min-height: 100%;

  @media (min-width: @tablet-width) {
    // для фикса:
    // width: 768px;
    // для резины:
    width: 100%;
  }

  @media (min-width: @desktop-width) {
    max-width: 1200px;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
  }
}

.main {
  flex-grow: 1;
  position: relative;
}

.main__link {
  font-weight: 700;
  text-decoration: none;
  color: @highlight-color;
  background-color: transparent;

  &--block {
    display: flex;
    padding: 0;
    margin: 0;
  }

  &--fixed {
    position: fixed;
    z-index: 2;
    right: 46px;
    bottom: 80px;

    @media (min-width: @desktop-width) {
      right: 60px;
    }
  }

  &:hover {
    color: rgb(213, 75, 36);
  }

  &:focus-visible {
    outline: 1px solid @highlight-color;
    outline-offset: 1px;
  }

  &:active {
    color: rgb(254, 186, 17);
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
