.footer {
  width: 100%;
  min-height: 70px;
  background: linear-gradient(to right, rgb(141, 201, 113) 50%, @separator-color 50%);
  padding: 0 52px;
  display: flex;
  justify-content: center;
}

.footer__wrapper {
  width: 350px;
  min-height: 70px;
  background-color: rgb(123, 194, 88);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
}

.footer__link-phone {
  letter-spacing: 2px;
  color: rgb(255, 255, 255);
  text-decoration: none;
  display: flex;
  width: 205px;
  justify-content: space-between;
  padding: 20px;

  &:hover {
    color: @text-color;
  }

  &:focus-visible {
    outline: 1px solid rgb(255, 255, 255);
    outline-offset: 2px;
  }

  &:active {
    color: @text-color;
    opacity: 0.5;
  }
}

.footer__text {
  font-size: 16px;
  line-height: 18px;
}
