@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/ptsans-400.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-style: italic;
  font-weight: 400;
  src: url("../fonts/ptsans-400-italic.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/ptsans-700.woff2") format("woff2");
  font-display: swap;
}
img {
  max-width: 100%;
  height: auto;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
.page {
  height: 100%;
}
.page__body {
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  color: #333333;
  height: 100%;
  margin: 0;
}
.page__container {
  display: flex;
  flex-direction: column;
  width: 320px;
  margin: 0 auto;
  min-height: 100%;
}
@media (min-width: 768px) {
  .page__container {
    width: 100%;
  }
}
@media (min-width: 1200px) {
  .page__container {
    max-width: 1200px;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
  }
}
.main {
  flex-grow: 1;
  position: relative;
}
.main__link {
  font-weight: 700;
  text-decoration: none;
  color: #f68c26;
  background-color: transparent;
}
.main__link--block {
  display: flex;
  padding: 0;
  margin: 0;
}
.main__link--fixed {
  position: fixed;
  z-index: 2;
  right: 46px;
  bottom: 80px;
}
@media (min-width: 1200px) {
  .main__link--fixed {
    right: 60px;
  }
}
.main__link:hover {
  color: #d54b24;
}
.main__link:focus-visible {
  outline: 1px solid #f68c26;
  outline-offset: 1px;
}
.main__link:active {
  color: #feba11;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.header {
  width: 100%;
  min-height: 70px;
  background: linear-gradient(to right, #d54b24, #feba11);
  padding: 10px 52px;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (min-width: 768px) {
  .header {
    padding-top: 20px;
    padding-bottom: 20px;
  }
}
.header__slogan {
  font-style: italic;
  font-size: 20px;
  line-height: 24px;
  letter-spacing: 0.05em;
  color: rgba(255, 255, 255, 0.8);
  padding: 0;
  margin: 0;
}
@media (min-width: 768px) {
  .header__slogan {
    font-size: 24px;
    line-height: 28px;
  }
}
.introduction {
  padding: 48px 24px 40px;
}
@media (min-width: 768px) {
  .introduction {
    padding: 48px 48px 40px;
  }
}
.introduction-wrapper {
  display: flex;
  flex-direction: column;
  margin-bottom: 48px;
}
@media (min-width: 768px) {
  .introduction-wrapper {
    flex-direction: row;
  }
}
.introduction__photo {
  object-fit: cover;
  width: 140px;
  height: 140px;
  margin-right: 24px;
  margin-bottom: 12px;
}
@media (min-width: 768px) {
  .introduction__photo {
    width: 255px;
    height: 255px;
    margin-right: 32px;
    margin-bottom: 0;
  }
}
.introduction__title {
  font-weight: 700;
  font-size: 28px;
  line-height: 32px;
  color: #f68c26;
  padding: 0;
  margin: 0 0 10px 0;
}
.introduction__text {
  padding: 0;
  margin: 0;
  text-indent: 1.5em;
}
.introduction__text--highlighting {
  text-decoration: underline #63ba45 solid;
  text-decoration-thickness: 3px;
  text-underline-offset: 3px;
}
.table {
  font-size: 14px;
  line-height: 17px;
  width: 100%;
  margin-bottom: 24px;
  overflow: hidden;
  border: none;
  border-spacing: 0;
  position: relative;
}
.table::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 5px;
  background: linear-gradient(to right, #702b7e, #c78dbd);
  bottom: -15px;
}
.table::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 5px;
  background: linear-gradient(to right, #702b7e, #c78dbd);
  top: -10px;
}
@media (min-width: 768px) {
  .table::before {
    display: none;
  }
}
.table__row:first-child {
  background: linear-gradient(to right, #702b7e, #c78dbd);
}
.table__header {
  display: none;
}
.table__data {
  display: block;
  position: relative;
}
@media (min-width: 768px) {
  .table__data {
    display: table-cell;
    vertical-align: top;
  }
}
.table__data:first-child {
  padding-top: 0.5em;
}
.table__data:last-child {
  padding-bottom: 0.5em;
}
.table__data::before {
  content: attr(data-th) ": ";
  font-weight: bold;
  width: 7em;
  display: inline-block;
  background: linear-gradient(to right, #702b7e, #c78dbd);
  padding-left: 0.5em;
  margin-right: 0.5em;
}
@media (min-width: 768px) {
  .table__data::before {
    display: none;
  }
}
.table__data--underlined:last-child {
  border-bottom: 2px solid #63ba45;
}
@media (min-width: 768px) {
  .table__data--underlined {
    border-bottom: 2px solid #63ba45;
  }
}
.table__header,
.table__data {
  text-align: left;
  margin: 0.5em 1em;
}
@media (min-width: 768px) {
  .table__header,
  .table__data {
    display: table-cell;
    padding: 1em;
    border-left: 1px solid #ffffff;
  }
  .table__header:last-child,
  .table__data:last-child {
    padding-right: 0;
  }
}
.table__header,
.table__data::before {
  color: #ffffff;
}
@media (min-width: 768px) {
  .table__header--1 {
    width: 10%;
  }
}
@media (min-width: 768px) {
  .table__header--2 {
    width: 15%;
  }
}
@media (min-width: 768px) {
  .table__header--3 {
    width: 30%;
  }
}
.portfolio {
  padding: 0 24px 58px 24px;
}
@media (min-width: 768px) {
  .portfolio {
    padding: 0 48px 58px 48px;
  }
}
.portfolio__title {
  font-weight: 700;
  font-size: 28px;
  line-height: 32px;
  color: #f68c26;
  padding: 0;
  margin: 0 0 35px 0;
}
.portfolio__subtitle {
  font-size: 24px;
  line-height: 28px;
  color: #333333;
  padding: 0;
  margin: 0 0 18px 0;
}
.portfolio__list {
  margin: 0;
  padding: 0;
  list-style: none;
  width: 100%;
  display: grid;
  gap: 28px;
  position: relative;
  margin-bottom: 48px;
}
@media (min-width: 768px) {
  .portfolio__list--three-columns {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1200px) {
  .portfolio__list--three-columns {
    grid-template-columns: repeat(3, 1fr);
  }
}
.portfolio__list:last-child::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 5px;
  background: linear-gradient(to right, #702b7e, #c78dbd);
  bottom: -48px;
}
.portfolio__item {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 0;
  padding: 4px;
  outline: 1px solid #63ba45;
}
@media (min-width: 768px) {
  .portfolio__item {
    flex-direction: row;
  }
}
.portfolio__link {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.portfolio__link:focus-visible {
  outline: 1px solid #f68c26;
  outline-offset: 2px;
}
.portfolio__image {
  width: 100%;
  object-fit: contain;
  display: block;
  background-color: #f6f6f6;
}
@media (min-width: 768px) {
  .portfolio__image--only-one {
    max-width: 349px;
  }
}
@media (min-width: 768px) {
  .portfolio__image--half {
    min-width: 331px;
  }
}
@media (min-width: 1200px) {
  .portfolio__image--half {
    max-width: 546px;
  }
}
.portfolio__image--half:first-child {
  margin: 0 0 4px 0;
}
@media (min-width: 768px) {
  .portfolio__image--half:first-child {
    margin: 0 4px 0 0;
  }
}
.footer {
  width: 100%;
  min-height: 70px;
  background: linear-gradient(to right, #8dc971 50%, #63ba45 50%);
  padding: 0 52px;
  display: flex;
  justify-content: center;
}
.footer__wrapper {
  width: 350px;
  min-height: 70px;
  background-color: #7bc258;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
}
.footer__link-phone {
  letter-spacing: 2px;
  color: #ffffff;
  text-decoration: none;
  display: flex;
  width: 205px;
  justify-content: space-between;
  padding: 20px;
}
.footer__link-phone:hover {
  color: #333333;
}
.footer__link-phone:focus-visible {
  outline: 1px solid #ffffff;
  outline-offset: 2px;
}
.footer__link-phone:active {
  color: #333333;
  opacity: 0.5;
}
.footer__text {
  font-size: 16px;
  line-height: 18px;
}
