.table {
  font-size: 14px;
  line-height: 17px;
  width: 100%;
  margin-bottom: 24px;
  overflow: hidden;
  border: none;
  border-spacing: 0;
  position: relative;

  // линейка под таблицей
  &::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 5px;
    background: linear-gradient(to right, rgb(112, 43, 126), rgb(199, 141, 189));
    bottom: -15px;
  }

  // линейка над таблицей в мобильной версии
  &::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 5px;
    background: linear-gradient(to right, rgb(112, 43, 126), rgb(199, 141, 189));
    top: -10px;

    @media (min-width: @tablet-width) {
      display: none;
    }
  }
}

.table__row:first-child {
  background: linear-gradient(to right, rgb(112, 43, 126), rgb(199, 141, 189));
}

.table__header {
  display: none;
}

.table__data {
  display: block;
  position: relative;

  @media (min-width: @tablet-width) {
    display: table-cell;
    vertical-align: top;
  }

  &:first-child {
    padding-top: 0.5em;
  }

  &:last-child {
    padding-bottom: 0.5em;
  }

  // вывод строк заголовка таблицы слева от ячеек в мобильной версии
  &::before {
    content: attr(data-th) ": ";
    font-weight: bold;
    width: 7em;
    display: inline-block;
    background: linear-gradient(to right, rgb(112, 43, 126), rgb(199, 141, 189));
    padding-left: 0.5em;
    margin-right: 0.5em;

    @media (min-width: @tablet-width) {
      display: none;
    }
  }
}

// разделительная линейка между блоками в мобильной версии
.table__data--underlined:last-child {
  border-bottom: 2px solid @separator-color;
}

// разделительная линейка между строками в планшете и десктопе
.table__data--underlined {
  @media (min-width: @tablet-width) {
    border-bottom: 2px solid @separator-color;
  }
}

.table__header,
.table__data {
  text-align: left;
  margin: 0.5em 1em;

  @media (min-width: @tablet-width) {
    display: table-cell;
    padding: 1em;
    border-left: 1px solid #ffffff;

    &:last-child {
      padding-right: 0;
    }
  }
}

.table__header,
.table__data::before {
  // цвет текста заголовка таблицы
  color: #ffffff;
}

.table__header--1 {
  @media (min-width: @tablet-width) {
    width: 10%;
  }
}

.table__header--2 {
  @media (min-width: @tablet-width) {
    width: 15%;
  }
}

.table__header--3 {
  @media (min-width: @tablet-width) {
    width: 30%;
  }
}
