.introduction {
  padding: 48px 24px 40px;

  @media (min-width: @tablet-width) {
    padding: 48px 48px 40px;
  }
}

.introduction-wrapper {
  display: flex;
  flex-direction: column;
  margin-bottom: 48px;

  @media (min-width: @tablet-width) {
    flex-direction: row;
  }
}

.introduction__photo {
  object-fit: cover;
  width: 140px;
  height: 140px;
  margin-right: 24px;
  margin-bottom: 12px;

  @media (min-width: @tablet-width) {
    width: 255px;
    height: 255px;
    margin-right: 32px;
    margin-bottom: 0;
  }
}

.introduction__title {
  font-weight: 700;
  font-size: 28px;
  line-height: 32px;
  color: @highlight-color;
  padding: 0;
  margin: 0 0 10px 0;
}

.introduction__text {
  padding: 0;
  margin: 0;
  text-indent: 1.5em;

  &--highlighting {
    text-decoration: underline @separator-color solid;
    text-decoration-thickness: 3px;
    text-underline-offset: 3px;
  }
}
